import { request } from "./request";


//请求首页数据
export function getHomeAllData() {
    return request({
        url: '/api/index',

    })
}

//请求首页推荐数据
export function getHomeGoods(type = 'sale', page = 1) {
    return request({
        url: '/api/index?' + type + '=1&page=' + page,

    })
}