<template>
  <div class="main-box">
    <div
      v-for="item in recommend.slice(0, 4)"
      :key="item.id"
      class="recommend-item"
    >
      <a href="" @click.prevent="goD(item.id)">
        <img :src="item.cover_url" />
        <div>{{ item.title }}</div>
      </a>
    </div>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
export default {
  name: "RecommendView",
  props: {
    recommend: {
      type: Array,
      defaylt() {
        return [];
      },
    },
  },

  setup(){

    const router = useRouter()
    const goD = (id) => {
      router.push({path:'./detail', query:{id}})
    }

    return{
      goD
    }
  }
};
</script>

<style lang="scss" scoped>
.main-box {
  margin-top: 10px;
  width: 95%;
  height: 100px;
  background-color: rgb(255, 255, 255);
  margin: 2.5%;
  color: black ;
  display: flex;
  font-size: var(--font-size2);
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  .recommend-item {
    width: 100px;
    height: 90px;
    img {
      width: 70px;
      height: 60px;
    }
  }
}
a{
    text-decoration: none;
}
</style>
