<template>
  <top-nav> 首页 </top-nav>
  <div id="main-box">
    <swipe :bunner="bunner"></swipe>
    <recommend-view :recommend="recommend"></recommend-view>
    <van-tabs v-model:active="active" class="van-tabs__nav--line" @click="tabClick">
      <van-tab title="精选"></van-tab>
      <van-tab title="新书"></van-tab>
      <van-tab title="推荐"></van-tab>
    </van-tabs>
    <goods-list :goods='showGoods'></goods-list>
  </div>
  <bottom-nav></bottom-nav>
</template>

<script>
import BottomNav from '../../components/BottomNav.vue'
import TopNav from "../../components/TopNav.vue";
import RecommendView from "./Children/RecommendView.vue";
import Swipe from "./Children/Swipe.vue";
import GoodsList from '../../components/GoodList.vue'
import { ref, reactive, onMounted, toRefs, computed } from "vue";
import { getHomeAllData, getHomeGoods } from "../../network/home";
import { Toast } from 'vant'
export default {
  name: "Home",
  components: { TopNav, Swipe, RecommendView, GoodsList,BottomNav },
  setup() {
    
    //推荐商品
    const goods = reactive({
      sales:{page:1, list:[]},
      recommend:{page:1, list:[]},
      new:{page:1, list:[]},
    })

    let currentType = ref('sales')
    const showGoods = computed(() => {
      return goods[currentType.value].list
    })

    const goodsRecommend = () => {
      const page = goods[currentType.value].page + 1
        getHomeGoods(currentType.value, page).then(res => {
          goods[currentType.value].list.push(...res.goods.data)
          goods[currentType.value].page += 1
        })
    }

    
    //点击切换商品推荐
    const tabClick = (index) => {
      const order = ["sales", "recommend", 'new']
      currentType.value = order[index]
      goodsRecommend()
    }
    
    //中间组件推荐数据
    const recommend = ref([]);

    //轮播图
    const active = ref(0)
    let bunner = ref([]);
    

    
    onMounted(async () => {
      Toast.loading({message:'加载中', forbidClick:true})
      await getHomeAllData().then((res) => {
        recommend.value = res.goods.data;
        bunner.value = res.slides;
      })

      await getHomeGoods('sales').then(res => {
        goods.sales.list = res.goods.data
      })

      await getHomeGoods('recommend').then(res => {
        goods.recommend.list = res.goods.data
      })

      await getHomeGoods('new').then(res => {
        goods.new.list = res.goods.data
      })
      goodsRecommend()

    });

    return {
      active,
      recommend,
      bunner,
      goods,
      showGoods,
      tabClick
    };
  },
};
</script>

<style lang="scss" scoped>
#main-box {
  width: 100%;
  margin-top: 50px;
}
.van-tabs__nav--line{
  width: 95% !important;
  margin: 2.5%;
  position: sticky;
  top: 50px;

}
</style>
>
