<template>
  <div class="goods-item" @click="itemClick(product.id)">
    <img :src="product.cover_url" />
    <div class="goods-info">
      <p>{{ product.title }}</p>
      <span class="price"><small>￥</small>{{ product.price }}</span>
      <span class="collect"
        ><i class="iconfont icon-shoucang"></i
        >{{ product.collects_count }}</span
      >
    </div>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'
export default {
  name: "GoodsListItem",
  props: {
    product: {
      type: Object,
      default() {
        return [];
      },
    },
  },
  setup(props) {
    const router = useRouter();
    return {
      itemClick: (id) => {
        router.push({ path: "/detail", query: { id: props.product.id } });
      },
    };
  },
};
</script>

<style lang="scss" scoped>
.goods-item {
  width: 46%;
  padding-bottom: 40px;
  overflow: hidden;
  img {
    width: 100%;
    border-radius: 10px;
  }
  .goods-info {
    .price {
      margin-right: 10px;
      color: red;
    }
  }
}
</style>
>
